.s-contacts {
  position: relative;
  padding-top: rem(100);
  padding-bottom: rem(100);
  &__map {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  &__block {
    position: relative;
    z-index: 1;
    max-width: 450px; }

  &--empty {
    height: 100vh; } }
